<template lang="pug">
.compDomainModifyDns
  h2
    strong {{detailData.domainName}}
    span DNS
  comp-radio(name="xinnet",:list="dnsList",ref="xinnet", :onParentmethod="changeRadio", defaultValue="1")
  div.radio1(v-show="dns==='1'")
    Button(type="primary",@click="submitXinnet",:loading="loadingBtn") 
  div.radio2(v-show="dns==='0'")
    Form(:label-width="200")
      FormItem(label="DNS",)
        comp-input(name='dns1',label="DNS",ref="dns1", :maxLength="50")
      FormItem(label="DNS",)
        comp-input(name='dns2',label="",ref="dns2", :maxLength="50")
      FormItem(label="DNS3",)
        comp-input(name='dns3',label="DNS3",ref="dns3", :maxLength="50")
      FormItem(label="DNS4",)
        comp-input(name='dns4',label="DNS4",ref="dns4", :maxLength="50")
      FormItem(label="DNS5")
        comp-input(name='dns5',label="DNS5",ref="dns5", :maxLength="50")
      FormItem(label="DNS6")
        comp-input(name='dns6',label="DNS6",ref="dns6", :maxLength="50")
      FormItem(label="")
        Button(type="primary",@click="submitNotXinnet",:loading="loadingBtn") 

  Alert.alert1(type="warning", show-icon) 
    Icon(custom="i-icon i-icon-notice_", size="16", slot="icon")
    template(slot="desc") 1<br />2AMXCNAME<br />3DNS24-48<br />4.gov.cnDNS
</template>

<script>
import {mapActions} from 'vuex'
import * as types from '@/store/types'
import compRadio from './compRadio'
import compInput from './compInput'
export default {
  name: 'compDomainMgmtDetailStep4',
  components: {
    compRadio,
    compInput
  },
  props: {
    verificationCode: {
      type: String,
      default: ''
    },
    detailData: {
      type: Object,
      default: function () {
        return {
          data: []
        }
      }
    }
  },
  data () {
    return {
      dns: '1',
      loadingBtn: false,
      dnsList: [
        {
          label: '1',
          value: 'DNSDNS'
        },
        {
          label: '0',
          value: 'DNSDNSDNSDNS'
        }
      ]
    }
  },
  methods: {
    changeRadio (obj) {
      this.dns = obj.value
    },
    submitXinnet () {
      this.loadingBtn = true
      let params = {
        param: {
          domainId: this.detailData.id,
          xinnet: 1,
          verificationCode: this.verificationCode,
          dns: ''
        },
        callback: (response) => {
          this.loadingBtn = false
          if (response.data.code === '1000') {
            this.$Message.success('DNS')
            let params = {
              param: {
                domainId: this.detailData.id
              },
              callback: (response) => {
                if (response.data.code === '1000') {
                  this.$emit('setDetailFun', response.data.data)
                } else {

                }
              }
            }
            this.queryDomainManageDetail(params)
          } else {
            if (response.data.code === '100') {
              this.$Message.error('')
            } else if (response.data.code === '200') {
              this.$Message.error('')
            } else if (response.data.code === '300') {
              this.$Message.error('')
            } else if (response.data.code === '400') {
              this.$Message.error('')
            } else {

            }
          }
        }
      }
      console.log(params.param)
      this.setDomainDns(params)
    },
    submitNotXinnet () {
      this.loadingBtn = true
      console.log(this.$refs)
      let arr = []
      for (var i = 1; i <= 6; i++) {
        if (this.$refs['dns' + i].value.length) {
          arr.push(this.$refs['dns' + i].value)
        }
      }
      if (arr.length < 2) {
        this.$Message.error('DNS')
        this.loadingBtn = false
      } else {
        let params = {
          param: {
            domainId: this.detailData.id,
            xinnet: 0,
            verificationCode: this.verificationCode,
            dns: arr.join(',')
          },
          callback: (response) => {
            this.loadingBtn = false
            if (response.data.code === '1000') {
              this.$Message.success('DNS')

              let params = {
                param: {
                  domainId: this.detailData.id
                },
                callback: (response) => {
                  if (response.data.code === '1000') {
                    this.$emit('setDetailFun', response.data.data)
                  } else {

                  }
                }
              }
              this.queryDomainManageDetail(params)
            } else {
              if (response.data.code === '100') {
                this.$Message.error('')
              } else if (response.data.code === '200') {
                this.$Message.error('')
              } else if (response.data.code === '300') {
                this.$Message.error('')
              } else if (response.data.code === '400') {
                this.$Message.error('')
              } else {

              }
            }
          }
        }
        console.log(params.param)
        this.setDomainDns(params)
      }
    },
    ...mapActions({
      setDomainDns: types.SET_DOMAIN_DNS,
      queryDomainManageDetail: types.QUERY_DOMAIN_MANAGE_DETAIL
    })
  },
  beforeMount () {
  },
  mounted () {

  },
  computed: {

  }
}
</script>
<style>
.compDomainModifyDns{
  margin: 0 20px;
  background: #fff;
}
.compDomainModifyDns h2{
  height: 60px;
  line-height: 60px;
  font-size: 16px;
  border-bottom: 1px solid #f0f0f0;
  font-weight: normal;
  padding: 0 20px;
}
.compDomainModifyDns h2 strong{
  color: #2071f6;
}
.compDomainModifyDns .compRadio{
  padding: 30px 20px;
}
.compDomainModifyDns .compRadio .ivu-radio-group-item{
  display: block;
}
.compDomainModifyDns .alert1{
  padding: 10px 10px 10px 32px;
  margin-bottom: 30px;
}
.compDomainModifyDns .alert1 .ivu-alert-icon{
  top: -3px;
  left: 10px;
  margin:0px;
}
.compDomainModifyDns .alert1 .ivu-alert-icon i{
  color: #ffad02;
}
.compDomainModifyDns .alert1 .ivu-alert-message{
  float: left;
  font-size: 12px;
  font-weight: 600;
  color: #666;
}
.compDomainModifyDns .alert1 .ivu-alert-desc{
  overflow: hidden;
}
.compDomainModifyDns .radio1{
  padding: 20px 0 20px 200px;
  border-bottom: 20px solid #fafafa;
}
.compDomainModifyDns .radio2{
  padding: 20px 0;
  border-bottom: 20px solid #fafafa;
}
</style>



