<template lang="pug">
  Form.compDomainMgmtDetailInfo(:label-width="150")
    FormItem(label="")
      span.text {{detailData.domainName}}
    FormItem(label="",)
      span.text {{detailData.applyDate}}
    FormItem(label="",)
      span.text {{detailData.expireDate}}
        <a href="javascript:;" v-show="detailData.depositFlag===1" @click="showOrder"></a>
        <a href="javascript:;" v-show="detailData.depositFlag===0" @click="renewFun"></a>
    FormItem(label="",)
      span.text {{detailData.organizeNameCn}}
        <a href="javascript:;" v-show="detailData.depositFlag===1" @click="showOrder"></a>
        router-link(to="/domain/change", v-show="detailData.depositFlag===0 && detailData.updateFlag===0") 
    FormItem(label="",)
      span.text {{detailData.userEmail}}
        <a href="javascript:;" v-show="detailData.depositFlag===1" @click="showOrder"></a>
        <a href="javascript:;" v-show="detailData.depositFlag===0 && detailData.updateFlag===0" @click="onTabschange(2)"></a>
    FormItem(label="")
      span.text {{detailData.verifyStatus ? this.DATAS.REAL_NAME_VERIFY_STATUS[detailData.verifyStatus] : ''}}
    FormItem(label="")
      p.text
        span.status(v-for="item in detailData.statusList") {{item.statusName}}
    FormItem(label="DNS")
      p.text
        span.dns(v-for="(item, index) in detailData.dnsList") {{item.dnsName}}
          <a href="javascript:;" v-show="!index && detailData.depositFlag===1" @click="showOrder"></a>
          <a href="javascript:;" v-show="!index && detailData.depositFlag===0 && detailData.updateFlag===0" @click="showDns"></a>

</template>

<script>
import {mapActions} from 'vuex'
import * as types from '@/store/types'
export default {
  name: 'compDomainMgmtDetailInfo',
  components: {
  },
  props: {
    detailData: {
      type: Object,
      default: function () {
        return {
          data: []
        }
      }
    },
    onTabschange: {
      type: Function
    }
  },
  data () {
    return {
    }
  },
  methods: {
    showOrder () {
      this.$emit('showWorkOrder')
    },
    showDns () {
      if (this.detailData.importantFlag) {
        this.$emit('showAuthorize')
      } else {
        this.$emit('modifyDns')
      }
    },
    renewFun () {
      var params = {
        param: {
          jsonObj: [{
            domainName: this.detailData.domainName,
            orderGoodsType: 2,
            orderType: 2
          }]
        },
        callback: (response) => {
          this.loadingBtn = false
          if (response.data.code === '1000') {
            response.data.type = '2_2'
            response.data.jsonObj.map((v) => {
              if (v.goodsNumAndPrice.length) {
                v.price = v.goodsNumAndPrice[0].price + '_' + v.goodsNumAndPrice[0].unit
                v.num = v.goodsNumAndPrice[0].num
                v.unit = v.goodsNumAndPrice[0].unit
              } else {
                v.price = '0_null'
                v.num = null
                v.unit = null
              }
            })
            this.$store.commit(types.SET_PAY_ORDERS, response.data)
            this.$router.push({path: '/payConfirm'})
          } else {
            if (response.data.code === '100') {
              this.$Message.error('')
            } else if (response.data.code === '200') {
              this.$Message.error('')
            } else if (response.data.code === '300') {
              this.$Message.error('')
            } else if (response.data.code === '400') {
              this.$Message.error('json')
            } else {
            }
          }
        }
      }
      console.log(params.param)
      this.queryOrderConfirm(params)
    },
    ...mapActions({
      queryOrderConfirm: types.QUERY_ORDER_CONFIRM
    })
  },
  beforeMount () {
  },
  mounted () {
  },
  computed: {

  }
}
</script>
<style scoped>
.compDomainMgmtDetailInfo{
  padding: 30px 0;
}
.compDomainMgmtDetailInfo .ivu-form-item .text a{
  display:inline-block;
  margin-left: 40px;
  color:#2071f6;
}
.compDomainMgmtDetailInfo .ivu-form-item .status{
  display: inline-block;
  margin-right: 20px;
}
.compDomainMgmtDetailInfo .ivu-form-item .dns{
  display:block;
}
</style>



